import Vuex from "vuex";
import axios from "axios";
import http from '@/utils/http.js'

export default {
	data() {
		return {
			headerTop: null,
			statusTop: null,
			currentSwiper: 0,
			// 轮播图片
			swiperList: [{
					id: 1,
					src: 'url1',
					img: '/static/img/1.jpg'
				},
				{
					id: 2,
					src: 'url2',
					img: '/static/img/2.jpg'
				},
				{
					id: 3,
					src: 'url3',
					img: '/static/img/3.jpg'
				}
			],
			// 分类菜单
			categoryList: [{
					id: 1,
					name: '口红/唇釉',
					enname: 'Lipstick',
					img: 'http://120.55.87.80/img/category/1.jpg'
				},
				{
					id: 2,
					name: '粉底/气垫',
					enname: 'Foundation',
					img: 'http://120.55.87.80/img/category/2.jpg'
				},
				{
					id: 3,
					name: '腮红/高光',
					enname: 'Blush',
					img: 'http://120.55.87.80/img/category/3.jpg'
				},
				{
					id: 4,
					name: '隔离/妆前',
					enname: 'Primer',
					img: 'http://120.55.87.80/img/category/4.jpg'
				},
				{
					id: 5,
					name: '定妆',
					enname: 'FixedMakeup',
					img: 'http://120.55.87.80/img/category/5.jpg'
				},
				{
					id: 6,
					name: '防晒',
					enname: 'Sunscreen',
					img: 'http://120.55.87.80/img/category/6.jpg'
				},
				{
					id: 7,
					name: '修容',
					enname: 'Modify',
					img: 'http://120.55.87.80/img/category/7.jpg'
				},
				{
					id: 8,
					name: '眼妆',
					enname: 'EyeMakeup',
					img: 'http://120.55.87.80/img/category/8.jpg'
				}
			],
			//猜你喜欢列表
			productList: []
		}
	},
	//此处用created相当于对前端页面数据进行初始化
	created() {
		var address = 'http://120.55.87.80/server/Recommend.php';
		http.post(address, 'Recommend').then(res => {
			//这里是ES6的写法，get请求的地址
			this.productList = res.data; //获取数据  
			console.log('success');
			console.log(this.productList);
		})
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//轮播图跳转
		toSwiper(e) {
			uni.showToast({
				title: e.src,
				icon: 'none'
			});
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		//分类跳转
		toCategory(e) {
			uni.setStorageSync('enName', e.enname);
			uni.setStorageSync('catName', e.name);
			uni.navigateTo({
				url: "../list/bigList",
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
		//商品跳转
		toGoods(e) {
			uni.setStorageSync('PHP', 'RecGoods');
			uni.setStorageSync('goodsName', e.name);
			uni.navigateTo({
				url: "../goods/goods",
				animationType: 'pop-in',
				animationDuration: 200
			});
		}
	}
}
